.slideshow {
  width: 100%;
  overflow: hidden;
  position: relative;
  display: flex;
  transition: transform 0.3s ease;
}

.slideshow img {
  min-width: 100%;
}

.slideshow-nav {
  font-size: 0;
  text-align: center;
  width: 50%;
  margin-top: 20px;
}

.tab {
  height: 5px;
  width: 33.33%;
  background-color: white;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.tab:hover {
  background-color: var(--main);
  opacity: 50%;
}

.active {
  background-color: var(--main);
}
